import { Component, OnInit } from '@angular/core';
import { PeopleService } from 'app/@core/data/remote/services/people.service';
import { People } from 'app/@core/data/remote/schemas/people';
import { NbDialogService } from '@nebular/theme';
import { AddPeopleComponent } from 'app/pages/users/people/add-people/add-people.component';
import { NbDialogRef } from '@nebular/theme';

@Component({
  selector: 'ngx-directivos',
  templateUrl: './directivos.component.html',
  styleUrls: ['./directivos.component.scss']
})
export class DirectivosComponent implements OnInit {
    people: People[];

    constructor(     private _dialog: NbDialogRef<DirectivosComponent>,

      private _peopleService : PeopleService, 
      private _dialogService: NbDialogService,) { }

    ngOnInit(): void {
        this._peopleService.getDirs().subscribe({
          next : ((people: People[]) => {
            this.people = people;
          }),
          error: ((err: any) => {
            // show error
            this._peopleService.showToast('top-right', 'danger', err);
          }),
        });
    }
 
    revealCard: boolean = false;
    person: People = new People();
    
    deletePerson(person: People, index: number){
  
      this._peopleService.deletePerson(person).subscribe({
          next : ((per: People) => {
              per && this.people.splice(index, 1);
              this._peopleService.showToast('top-right', 'success', 'Borrardo correctamente')
          }),
          error: ((err: any) => {
            // show error
            this._peopleService.showToast('top-right', 'danger', err);
          }),
        });
    }
  
    editPerson(person: People, index: number){
      this.revealCard = !this.revealCard;
      this._dialogService.open(AddPeopleComponent, {context:{_person: person, _isColab: false}})
      .onClose.subscribe( per => {
        if (per) {
       
          Object.assign(this.people[index], per);
  
        }
  
      });
    }
    toggleCard(){
        this.revealCard = !this.revealCard;
        this.person.tag = '1';
        this._dialogService.open(AddPeopleComponent, {context:{_person: this.person,_isColab:false}})
        .onClose.subscribe( person => {
          if (person) {
            this.people.push(person);
          }
  
        });
    }
  users = [
    {   'name': 'Humberto Rangel Ávila, Ph.D',
        'rg': 'https://www.researchgate.net/profile/Humberto_Avila/publications',
        'avatar': 'assets/img/users/FotoHumbertoAvila.png',
        'cargo': 'Director del Observatorio del Río Magdalena',
    },
    {   'name': 'Augusto Herminio Sisa Camargo, M.Sc',
        'rg': 'https://www.researchgate.net/profile/Augusto_Sisa',
        'avatar': 'assets/img/users/FotoAugustoSisa.png',
        'cargo': 'Docente tiempo completo Dpto. de Ing.Civil y Ambiental',
    },
    {   'name': 'Luis Rojano, Ing.',
        'rg': 'http://scienti.colciencias.gov.co:8081/cvlac/visualizador/generarCurriculoCv.do?cod_rh=0001582654',
        'avatar': 'assets/img/users/FotoLuisRojano.png',
        'cargo': 'Ingeniero de Software',
    },
    {   'name': 'Wilson Nieto Bernal, Ph.D',
        'rg': 'https://www.researchgate.net/profile/Wilson_Bernal',
        'avatar': 'assets/img/users/FotoWilsonNieto.png',
        'cargo': 'Docente tiempo completo Dpto. de Ing. de Sistemas',
    } 
]
close() {
  this._dialog.close();
}
}
