import { Component, OnInit } from '@angular/core';
import { PeopleService } from 'app/@core/data/remote/services/people.service';
import { People } from 'app/@core/data/remote/schemas/people';
import { NbDialogService } from '@nebular/theme';
import { AddPeopleComponent } from '../../../users/people/add-people/add-people.component';
import { NbDialogRef } from '@nebular/theme';

@Component({
  selector: 'ngx-colaborators',
  templateUrl: './colaborators.component.html',
  styleUrls: ['./colaborators.component.scss']
})
export class ColaboratorsComponent implements OnInit {
  people: People[];
  revealCard: boolean = false;
  person: People = new People();
  constructor(
    private _dialog: NbDialogRef<ColaboratorsComponent>,
    private _peopleService : PeopleService, 
    private _dialogService: NbDialogService,
    ) { }

  ngOnInit(): void {
    this._peopleService.getColabs().subscribe({
      next : ((people) => {
        this.people = people;
      }),
      error: ((err: any) => {
        // show error
        this._peopleService.showToast('top-right', 'danger', err);
      }),
    });
  }
 
  deletePerson(person: People, index: number){

    this._peopleService.deletePerson(person).subscribe({
        next : ((per: People) => {
            per && this.people.splice(index, 1);
            this._peopleService.showToast('top-right', 'success', 'Borrardo correctamente')
        }),
        error: ((err: any) => {
          // show error
          this._peopleService.showToast('top-right', 'danger', err);
        }),
      });
  }

  editPerson(person: People, index: number){
    this.revealCard = !this.revealCard;
    this._dialogService.open(AddPeopleComponent, {context:{_person: person, _isColab: true}})
    .onClose.subscribe( per => {
      if (per) {

        Object.assign(this.people[index], new People().deserialize(per));

      }

    });
  }
  toggleCard(){
      this.revealCard = !this.revealCard;
      this.person.tag = '0';
      this._dialogService.open(AddPeopleComponent, {context:{_person: this.person,_isColab:true}})
      .onClose.subscribe( person => {
        if (person) {
          this.people.push(person);
        }

      });
  }
  close() {
    this._dialog.close();
  }
}
